  override val projects_master_system_shortDescription: String =
      "Aplikacja do zarzdzania turniejami z niestandardowym algorytmem"

  override val projects_master_system_description: String =
      "Ta aplikacja do zarzdzania turniejami zostaa opracowana na zlecenie, aby uatwi rozgrywki wedug algorytmu specyficznego dla klienta. Aplikacja zapewnia przyjazny interfejs do wprowadzania wynikw meczw, automatycznie paruje druyny na kolejn rund na podstawie algorytmu, oblicza punkty i generuje podsumowania rozgrywek w postaci pliku PDF. Aplikacja wykonuje take grafy, ktre reprezentuj logik algorytmu parowania druyn. Aplikacja jest przetumaczona na cztery jzyki za pomoc moduu gettext w Pythonie, co zapewnia dostpno dla zrnicowanej grupy uytkownikw. Ten projekt pokazuje moj zdolno do dostarczania dostosowanych, wielojzycznych rozwiza programowych, ktre speniaj specyficzne wymagania klienta i usprawniaj zarzdzanie zoonymi strukturami turniejowymi."

  override val projects_vl_shortDescription: String =
      "Valorant Lineups jest aplikacj mobiln dla graczy Valoranta, ktra pomaga im podczas rozgrywki"

  override val projects_vl_description: String =
      "Valorant Lineups jest aplikacj mobiln dla graczy Valoranta, ktra pomaga im podczas rozgrywki. Aplikacja oferuje szerok kolekcj lineupw, umoliwiajc graczom wykonywanie zaawansowanych technik bez koniecznoci wczeniejszego zapamitywania specyficznych ustawie na mapie. Dziki aplikacji gracze mog osiga lepsze rezultaty w grze i zdobywa wysze rangi. Bya to pierwsza aplikacja tego typu do Valoranta dostpna na sklepie Play, osigna sukecs w postaci ponad 100 tysicy pobra. Ten projekt podkrela moje umiejtnoci w dostarczaniu innowacyjnych i skutecznych projektw."

  override val projects_demiurg_shortDescription: String =
      "Demiurg to aplikacja opracowana w ramach kursu Inynierii Oprogramowania, stworzona przez zesp piciu osb."

  override val projects_demiurg_description: String =
      "Demiurg to aplikacja opracowana w ramach kursu Inynierii Oprogramowania, stworzona przez zesp piciu osb. Projekt skada si z dwch osobnych aplikacji: kreatora i interpretatora gier tekstowych. Kreator pozwala uytkownikom projektowa zoone wiaty gier z mechanikami takimi jak nieliniowe zadania. Umoliwia tworzenie szczegowych interaktywnych narracji, podczas gdy interpretator zapewnia immersyjne dowiadczenie podczas grania. Ten projekt pokazuje moj umiejtno do pracy w zespole, jak i tworzenie niezalenych, ale jednoczenie kooperujcych programw."

  override val projects_daymath_shortDescription: String =
      "Aplikacja edukacyjna dla polskich uczniw, zawierajca quizy matematyczne, wyjanienia i animacje."

  override val projects_daymath_description: String =
      "DayMath to aplikacja edukacyjna, skierowana do uczniw polskich szk podstawowych i rednich. Aplikacja ma na celu usprawnienie nauki matematyki poprzez rnorodne interaktywne funkcje, w tym quizy matematyczne, szczegowe tekstowe wyjanienia rnych tematw oraz animacje wizualizujce i wyjaniajce zoone problemy. Te narzdzia wsppracuj ze sob, aby zapewni angaujce i skuteczne dowiadczenie edukacyjne. Jest to mj najbardziej zaawansowany projekt, ktry czy wiele technologii. Sama aplikacja jest wykonana w Kotlinie, jednak teksty do zada wymagay uycia LaTeXa, a ten z kolei jest dobrze wspierany w Pythonie. Aplikacja czy si take z Firebasem, gdzie zapisywane s postpy uytkonikw w nauce. Ten projekt podkrela moje zaangaowanie w rozwj mojej dziaalnoci korepetytora oraz czy moje umiejtnoci informatyczne, matematyczne, edukacyjne oraz lingwistyczne."

  override val projects_movie_ai_shortDescription: String =
      "Aplikacja desktopowa rekomendujca filmy na podstawie ocen uytkownika, rozwijana na kursie podstaw sztucznej inteligencji."

  override val projects_movie_ai_description: String =
      "System Rekomendacji Filmw to aplikacja desktopowa stworzona we wsppracy z koleg w ramach kursu Podstaw Sztucznej Inteligencji. Ta aplikacja umoliwia uytkownikom ocenianie filmw, ktre obejrzeli, i wykorzystuje algorytmy AI do rekomendowania nowych filmw dostosowanych do ich preferencji. Analizujc oceny uytkownikw i stosujc zaawansowane techniki rekomendacji, system dostarcza spersonalizowane propozycje filmw, poprawiajc dowiadczenie uytkownika zwizanego z ogldaniem. Ten projekt podkrela nasze praktyczne zastosowanie zasad AI i nasz zdolno do tworzenia inteligentnych rozwiza programowych zorientowanych na uytkownika."

