  override val projects_master_system_shortDescription: String =
      "Application for managing tournaments with a custom algorithm"

  override val projects_master_system_description: String =
      "This tournament management application was developed on commission to facilitate competitive match play according to a client-specific algorithm. The app provides a user-friendly interface for entering match results, automatically pairs teams for the next round based on the algorithm, calculates points, and generates comprehensive PDF summaries of the tournament's progress. Additionally, it creates graphs to visually represent the algorithm's logic and outcomes. The application is translated into four languages using Python's gettext module, ensuring accessibility for a diverse user base. This project showcases my ability to deliver tailored, multilingual software solutions that meet specific client requirements and enhance the management of complex tournament structures."

  override val projects_vl_shortDescription: String =
      "Valorant Lineups is a mobile app for Valorant players that helps them during gameplay"

  override val projects_vl_description: String =
      "Valorant Lineups is a mobile app created for Valorant players to assist them during gameplay. The app offers a wide collection of lineups, allowing players to execute advanced techniques without the need to memorize specific settings on the map. With this app, players can achieve better results in the game and climb higher ranks. It was the first app of its kind for Valorant available on the Play Store, achieving success with over 100,000 downloads. This project highlights my skills in delivering innovative and effective solutions."

  override val projects_demiurg_shortDescription: String =
      "Demiurg is an application developed as part of a Software Engineering course, created by a team of five people."

  override val projects_demiurg_description: String =
      "Demiurg is an application developed as part of a Software Engineering course, created by a team of five people. The project consists of two separate applications: a game creator and an interpreter for text-based games. The creator allows users to design complex game worlds with mechanics such as non-linear quests. It enables the creation of detailed interactive narratives, while the interpreter provides an immersive experience during gameplay. This project demonstrates my ability to work in a team and create independent yet cooperating programs."

  override val projects_daymath_shortDescription: String =
      "DayMath is an educational application for Polish students, featuring math quizzes, explanations, and animations."

  override val projects_daymath_description: String =
      "DayMath is an educational application designed for Polish primary and secondary school students. The app aims to enhance math learning through a variety of interactive features, including math quizzes, detailed text explanations of various topics, and animations that visualize and explain complex problems. These tools work together to provide an engaging and effective educational experience. It is my most advanced project, combining multiple technologies. The app itself is built in Kotlin, but the task texts required the use of LaTeX, which is well-supported in Python. The app also connects to Firebase, where users' learning progress is saved. This project highlights my commitment to developing my tutoring business and combines my skills in computer science, mathematics, education, and linguistics."

  override val projects_movie_ai_shortDescription: String =
      "Desktop app recommending movies based on user ratings, developed in an AI fundamentals course."

  override val projects_movie_ai_description: String =
      "The Movie Recommendation System is a desktop application created in collaboration with a colleague as part of a Fundamentals of Artificial Intelligence course. This app enables users to rate movies they have watched, and employs AI algorithms to recommend new movies tailored to their preferences. By analyzing user ratings and employing sophisticated recommendation techniques, the system provides personalized movie suggestions, enhancing the users viewing experience. This project highlights our practical application of AI principles and our ability to develop user-centric, intelligent software solutions."

  override val projects_generator_tabliczek_shortDescription: String =
